import * as React from 'react';
import Grid from '@mui/material/Grid';
import Link from '@mui/material/Link';
import Typography from '@mui/material/Typography';
import Logo from '../../images/logo.svg';
import MailIcon from '../../images/icon/contact_mail.svg';
import LineIcon from '../../images/icon/contact_line.svg';
import TwitterIcon from '../../images/icon/contact_twitter.svg';
import InstagramIcon from '../../images/icon/contact_instagram.svg';
import { scrollToSection } from '../../utils/scroll';
import { Section } from '../section';
import { SectionTitle } from '../section-title';

const mail_address: string = 'addict.sdgy@gmail.com';

const List = ({ titles }: { titles: string[] }) => (
  <ul
    style={{
      margin: 0,
      padding: 0,
      listStyleType: 'none'
    }}
  >
    {titles.map((title: string, i: number) => {
      const lowerTitle: string = title.toLowerCase();

      return (
        <li
          key={i}
          style={{
            marginBottom: '1em'
          }}
        >
          <Link
            href={`#${lowerTitle}`}
            underline='hover'
            color='#fff'
            onClick={e => {
              e.preventDefault();
              scrollToSection(lowerTitle);
            }}
          >
            <Typography
              sx={{
                textAlign: {
                  sm: 'left',
                  xs: 'center'
                }
              }}
            >
              {title}
            </Typography>
          </Link>
        </li>
      );
    })}
  </ul>
);

