import * as React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { StaticImage, GatsbyImage, getImage } from 'gatsby-plugin-image';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import { Section } from '../section';
import { SectionTitle } from '../section-title';

const GridText = () => (
  <Grid
    size={{
      md: 5,
      xs: 12
    }}
  >
    <Box>
      <SectionTitle align='left'>Facility</SectionTitle>
      <Typography
        sx={{
          mb: 4,
          lineHeight: 2
        }}
      >
        32
      </Typography>
    </Box>
  </Grid>
);

const GridImage = () => (
  <Grid
    size={{
      md: 6,
      xs: 12
    }}
  >
    <Box>
      <StaticImage
        src='../../images/1200x800-facility.jpg'
        alt='Facility'
        layout='fullWidth'
      />
    </Box>
  </Grid>
);

export const SectionFacility = () => {
  const data = useStaticQuery(graphql`
    query {
      allFile(filter: { relativeDirectory: { eq: "facilities" } }) {
        edges {
          node {
            name
            childImageSharp {
              gatsbyImageData
            }
          }
        }
      }
    }
  `);

  return (
    <Section id='facility'>
      <Grid
        container
        justifyContent='space-between'
        sx={{
          display: {
            md: 'none'
          }
        }}
      >
        <GridText />
        <GridImage />
      </Grid>
      <Grid
        container
        justifyContent='space-between'
        sx={{
          display: {
            xs: 'none',
            md: 'flex'
          }
        }}
      >
        <GridImage />
        <GridText />
      </Grid>

      <Grid
        container
        spacing={3}
        sx={{
          marginTop: 12
        }}
      >
        {[
          {
            fileName: '900x600-facility-01',
            alt: '32'
          },
          {
            fileName: '900x600-facility-02',
            alt: '2'
          },
          {
            fileName: '900x600-facility-03',
            alt: ''
          }
        ].map(({ fileName, alt }, i) => {
          const { node } = data.allFile.edges.find(
            (edge: any) => edge.node.name === fileName
          );

          const imageData: any = getImage(node);

          return (
            <Grid
              key={i}
              size={{
                sm: 4,
                xs: 12
              }}
              sx={{
                mt: 3
              }}
            >
              <Box>
                <GatsbyImage image={imageData} alt={alt} />
                <Typography
                  sx={{
                    mt: 2
                  }}
                >
                  {alt}
                </Typography>
              </Box>
            </Grid>
          );
        })}
      </Grid>

      <Grid
        container
        spacing={3}
        sx={{
          mt: 3
        }}
      >
        {[
          '600x600-facility-04',
          '600x600-facility-05',
          '600x600-facility-06',
          '600x600-facility-07'
        ].map((fileName, i) => {
          const { node } = data.allFile.edges.find(
            (edge: any) => edge.node.name === fileName
          );
          const imageData: any = getImage(node);

          return (
            <Grid
              key={i}
              size={{
                md: 3,
                xs: 6
              }}
              sx={{
                mt: 3
              }}
            >
              <Box>
                <GatsbyImage image={imageData} alt={`Facility${i + 1}`} />
              </Box>
            </Grid>
          );
        })}
      </Grid>
    </Section>
  );
};
