import * as React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Link from '@mui/material/Link';
import Typography from '@mui/material/Typography';
import { Section } from '../section';
import { SectionTitle } from '../section-title';
import InstagramIcon from '../../images/icon/trainer_instagram.svg';
// import TwitterIcon from '../../images/icon/trainer_twitter.svg';
// import TiktokIcon from '../../images/icon/trainer_tiktok.svg';

export const SectionTrainer = () => {
  const data = useStaticQuery(graphql`
    query {
      otomo: file(relativePath: { eq: "640x640-trainer-otomo.jpg" }) {
        childImageSharp {
          gatsbyImageData
        }
      }
      calvin: file(relativePath: { eq: "640x640-trainer-calvin.jpg" }) {
        childImageSharp {
          gatsbyImageData
        }
      }
      kashima: file(relativePath: { eq: "640x640-trainer-kashima.jpg" }) {
        childImageSharp {
          gatsbyImageData
        }
      }
      kohinata: file(relativePath: { eq: "640x640-trainer-kohinata.jpg" }) {
        childImageSharp {
          gatsbyImageData
        }
      }
    }
  `);

  return (
    <Section id='trainer' theme='light'>
      <SectionTitle theme='light'>Trainer</SectionTitle>
      <Grid container spacing={4}>
        {[
          {
            image: data.otomo,
            name: ' ',
            en_name: 'Miki Otomo',
            description:
              'WFF Bikini Pro',
            sns: []
          }
          /*
          {
            image: data.calvin,
            name: '',
            en_name: 'Joe',
            description:
              '',
            sns: [
              {
                image: InstagramIcon,
                link: 'https://www.instagram.com/workout828official/'
              }
            ]
          },
          {
            image: data.kashima,
            name: ' ',
            en_name: 'Yoshinari Kashima',
            description:
              '',
            sns: []
          },
          {
            image: data.kohinata,
            name: ' ',
            en_name: 'Daisuke Kohinata',
            description:
              ' ',
            sns: [
              {
                image: InstagramIcon,
                link: 'https://www.instagram.com/addisium/'
              }
            ]
          }
          */
        ].map(({ image, name, en_name, description, sns }, i) => {
          const imageData: any = getImage(image);
          return (
            <Grid
              key={i}
              size={{
                md: 6
              }}
            >
              <Grid container spacing={3}>
                <Grid
                  size={{
                    sm: 6,
                    xs: 12
                  }}
                >
                  <GatsbyImage image={imageData} alt={en_name} />
                </Grid>
                <Grid
                  size={{
                    sm: 6,
                    xs: 12
                  }}
                >
                  <Typography
                    sx={{
                      fontSize: '24px',
                      lineHeight: '26px'
                    }}
                  >
                    {name}
                  </Typography>
                  <Typography
                    sx={{
                      fontSize: '12px',
                      lineHeight: '26px',
                      letterSpacing: '0.6px',
                      color: '#b28223'
                    }}
                  >
                    {en_name}
                  </Typography>
                  <Typography
                    sx={{
                      marginTop: '1em',
                      lineHeight: '26px'
                    }}
                  >
                    {description}
                  </Typography>
                  <Box
                    sx={{
                      mt: 1
                    }}
                  >
                    {sns.map(({ image, link }, i) => (
                      <Link
                        key={i}
                        href={link}
                        target='_blank'
                        sx={{
                          mr: 1
                        }}
                      >
                        <img src={image} alt={name} width={36} height={36} />
                      </Link>
                    ))}
                  </Box>
                </Grid>
              </Grid>
            </Grid>
          );
        })}
      </Grid>
      <Typography variant='body2' align='center' mt={10}>
         Trainer {' '}
        <Link
          href='https://works.do/R/ti/p/addictgymjp@addictgym'
          target='_blank'
          rel='noopener noreferrer'
          sx={{
            '&:hover': {
              color: '#2e2e2e'
            }
          }}
        >
           LINE
        </Link>{' '}
        
      </Typography>
    </Section>
  );
};
