import * as React from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { Seo } from '../components/seo';
import { SectionMenu } from '../components/sections/menu';
import { SectionMainvisual } from '../components/sections/mainvisual';
import { SectionAbout } from '../components/sections/about';
import { SectionFacility } from '../components/sections/facility';
import { SectionPricing } from '../components/sections/pricing';
import { SectionBooking } from '../components/sections/booking';
import { SectionTrainer } from '../components/sections/trainer';
import { SectionAccess } from '../components/sections/access';
import { SectionContact } from '../components/sections/contact';
import { SectionBackgroundAddictgym } from '../components/sections/background-addictgym';
import '../index.css';

const theme = createTheme({
  palette: {
    primary: {
      main: '#b28223'
    }
  },
  typography: {
    fontSize: 12,
    fontFamily: ['YuMincho', 'serif'].join(','),
    h4: {
      fontSize: 32,
      letterSpacing: 1.5
    },
    h5: {
      fontSize: 28
    }
  }
});

const IndexPage = () => {
  const [openingAnimation, setOpeningAnimation] = React.useState(0);
  const [isShow, setIsShow] = React.useState(false);

  React.useEffect(() => {
    // @TODO Opening animation
    const { searchParams } = new URL(window.location.href);
    const openingAnimation = Number(searchParams.get('openingAnimation'));

    if (!!openingAnimation) {
      setTimeout(() => {
        setIsShow(true);
      }, 1000);
    } else {
      setIsShow(true);
    }

    setOpeningAnimation(openingAnimation);
  }, []);

  return (
    <ThemeProvider theme={theme}>
      {openingAnimation === 1 && (
        <>
          <div
            style={{
              position: 'fixed',
              top: 0,
              left: 0,
              width: '100%',
              height: '50vh',
              boxSizing: 'border-box',
              borderBottom: '1px solid #b28223',
              backgroundColor: '#000',
              transformOrigin: 'top',
              transition: 'transform 500ms ease-out',
              transform: `scaleY(${isShow ? 0 : 1})`,
              zIndex: 2000
            }}
          />
          <div
            style={{
              position: 'fixed',
              bottom: 0,
              left: 0,
              width: '100%',
              height: '50vh',
              boxSizing: 'border-box',
              borderTop: '1px solid #b28223',
              backgroundColor: '#000',
              transformOrigin: 'bottom',
              transition: 'transform 500ms ease-out',
              transform: `scaleY(${isShow ? 0 : 1})`,
              zIndex: 2000
            }}
          />
        </>
      )}
      {isShow && (
        <>
          <SectionMenu />
          <SectionMainvisual />
          <SectionAbout />
          <SectionBackgroundAddictgym />
          <SectionFacility />
          <SectionPricing />
          <SectionBooking />
          <SectionTrainer />
          <SectionAccess />
          <SectionContact />
        </>
      )}
    </ThemeProvider>
  );
};

export default IndexPage;

export const Head = () => <Seo />;
